"use client"

import { zodResolver } from "@hookform/resolvers/zod"
import { useForm } from "react-hook-form"
import * as z from "zod"
import { Button } from "@/components/ui/button"
import {
    Form,
    FormControl,
    FormDescription,
    FormField,
    FormItem,
    FormLabel,
    FormMessage,
} from "@/components/ui/form"
import { Input } from "@/components/ui/input"
import { Textarea } from "@/components/ui/textarea"
import {
    Select,
    SelectContent,
    SelectItem,
    SelectTrigger,
    SelectValue,
} from "@/components/ui/select"
import { useData, Service } from "@/lib/data-context"
import { useRouter } from "next/navigation"
import { iconMap } from "@/lib/icons"
import { useEffect } from "react"

const formSchema = z.object({
    title: z.string().min(2, {
        message: "Title must be at least 2 characters.",
    }),
    description: z.string().min(10, {
        message: "Description must be at least 10 characters.",
    }),
    iconName: z.string({
        required_error: "Please select an icon.",
    }),
    color: z.string().min(4, {
        message: "Color must be a valid hex code.",
    }),
    image: z.string().min(1, {
        message: "Image URL is required.",
    }),
    features: z.string().min(1, {
        message: "Features are required (comma separated).",
    }),
})

interface ServiceFormProps {
    initialData?: Service
    isEditing?: boolean
}

export function ServiceForm({ initialData, isEditing = false }: ServiceFormProps) {
    const { addService, updateService } = useData()
    const router = useRouter()

    const form = useForm<z.infer<typeof formSchema>>({
        resolver: zodResolver(formSchema),
        defaultValues: {
            title: initialData?.title || "",
            description: initialData?.description || "",
            iconName: initialData?.iconName || "",
            color: initialData?.color || "#7B3FEF",
            image: initialData?.image || "/placeholder.svg",
            features: initialData?.features.join(", ") || "",
        },
    })

    function onSubmit(values: z.infer<typeof formSchema>) {
        const serviceData = {
            ...values,
            features: values.features.split(",").map((f) => f.trim()).filter((f) => f !== ""),
            href: initialData?.href || `/services/${values.title.toLowerCase().replace(/\s+/g, "-")}`,
        }

        if (isEditing && initialData) {
            updateService(initialData.id, serviceData)
        } else {
            addService(serviceData)
        }

        router.push("/dashboard/services")
        router.refresh()
    }

    return (
        <Form {...form}>
            <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-8 max-w-2xl">
                <FormField
                    control={form.control}
                    name="title"
                    render={({ field }) => (
                        <FormItem>
                            <FormLabel>Title</FormLabel>
                            <FormControl>
                                <Input placeholder="Service Title" {...field} />
                            </FormControl>
                            <FormMessage />
                        </FormItem>
                    )}
                />
                <FormField
                    control={form.control}
                    name="description"
                    render={({ field }) => (
                        <FormItem>
                            <FormLabel>Description</FormLabel>
                            <FormControl>
                                <Textarea placeholder="Service Description" {...field} />
                            </FormControl>
                            <FormMessage />
                        </FormItem>
                    )}
                />
                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                    <FormField
                        control={form.control}
                        name="iconName"
                        render={({ field }) => (
                            <FormItem>
                                <FormLabel>Icon</FormLabel>
                                <Select onValueChange={field.onChange} defaultValue={field.value}>
                                    <FormControl>
                                        <SelectTrigger>
                                            <SelectValue placeholder="Select an icon" />
                                        </SelectTrigger>
                                    </FormControl>
                                    <SelectContent>
                                        {Object.keys(iconMap).map((iconName) => (
                                            <SelectItem key={iconName} value={iconName}>
                                                {iconName}
                                            </SelectItem>
                                        ))}
                                    </SelectContent>
                                </Select>
                                <FormMessage />
                            </FormItem>
                        )}
                    />
                    <FormField
                        control={form.control}
                        name="color"
                        render={({ field }) => (
                            <FormItem>
                                <FormLabel>Accent Color</FormLabel>
                                <FormControl>
                                    <div className="flex gap-2">
                                        <Input type="color" {...field} className="w-12 p-1 h-10" />
                                        <Input {...field} placeholder="#000000" />
                                    </div>
                                </FormControl>
                                <FormMessage />
                            </FormItem>
                        )}
                    />
                </div>
                <FormField
                    control={form.control}
                    name="image"
                    render={({ field }) => (
                        <FormItem>
                            <FormLabel>Image URL</FormLabel>
                            <FormControl>
                                <Input placeholder="/path/to/image.png" {...field} />
                            </FormControl>
                            <FormDescription>
                                Enter the path to the image in the public folder.
                            </FormDescription>
                            <FormMessage />
                        </FormItem>
                    )}
                />
                <FormField
                    control={form.control}
                    name="features"
                    render={({ field }) => (
                        <FormItem>
                            <FormLabel>Features</FormLabel>
                            <FormControl>
                                <Textarea placeholder="Feature 1, Feature 2, Feature 3" {...field} />
                            </FormControl>
                            <FormDescription>
                                Separate features with commas.
                            </FormDescription>
                            <FormMessage />
                        </FormItem>
                    )}
                />
                <Button type="submit">{isEditing ? "Update Service" : "Create Service"}</Button>
            </form>
        </Form>
    )
}
